h3 {
  color: green;
  font-style: italic;
}

h2 {
  color: green;
  font-style: italic;
}

.note {
  color: grey;
  padding-top: 10px;
  font-size: 15px;
}

.phone {
  color: blue;
  padding-top: 10px;
  font-style: italic;
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
  background: lightcoral;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.error {
  display: none;
  color: red;
  background: lightblue;
  font-size: 20px;
  border-style: inset;
  border-radius: 30px;
  padding: 20px 20px 20px 20px;
  margin-bottom: 20px;
}

.form-name, .form-number {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-submit{
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-submit:hover {
  background-color: #45a049;
}